import axios  from "axios";
import store from'@/store'
const reqaxios=axios.create({
  baseURL: "http://gmall-h5-api.atguigu.cn",
  //请求不能超过5S
  timeout: 5000,
})
reqaxios.interceptors.request.use((config)=>{
  
  if (store.state.Dlu.token) {
    config.headers.token = store.state.Dlu.token
  }
  

  return config
})

export default reqaxios