    dddianj(a, b) {
      b.forEach((i) => {
        i.isChecked = "0";
      });
      a.isChecked = "1";
    },
    jian() {
      if (this.suliang == 0) {
        this.suliang = 0;
      } else {
        this.suliang -= 1;
      }
    },
    async gowuctianj(skuId,skuNum){
      await this.$store.dispatch('jiarugowuc',{skuId,skuNum})

    },
    aaaa(){
      this.gowuctianj(this.k,this.suliang)
      console.log(this.k,'****---++++');
      this.$router.push({path:'/gowuc'})
      
    },
    async gowuc() {
      if (this.$store.state.Dlu.token) {
        await this.gowuctianj(this.k,this.suliang)
        this.$router.push({path:'/gowuc'})
      }else{
        this.$router.push({path:'/login'})
      }

      
    },
  },
  created() {
    this.k = this.$route.query.id;
    this.getskuId();
  },
  updated() {},
};
</script>

