import axiospost from '@/utlis/requext.js'
import axiox1 from '@/utlis/requext.js'
import reqaxios from'@/api/ajax.js'

export const loginpots = function (phone, password){
  return axiospost.post('/api/user/passport/login',{
    params: {
      phone,
      password
    }
    
  })
}


                                        ////登录功能接口////

  //注册接口 用户名 密码 验证码/api/user/passport/register'
export const postzuce = (data) => { return axiox1.post('/api/user/passport/register',data)}

  //验证码接口 手机号
export const getyanzma = (tel) => { return axiox1.get(`/api/user/passport/sendCode/${tel}`)}

  //登录接口 用户名/手机号 密码
export const postdenglu = (dl) => { return axiox1.post('/api/user/passport/login',dl)}

  //用户信息
export const getyhuxingxi = () => { return reqaxios.get(`/api/user/passport/auth/getUserInfo/`); }

  //退出登录
export const gettueicudenglu = () => { return axiox1.get('/api/user/passport/logout')}


                                        ////购物车接口////

  //获取购物车列表
export const getgowuclbiao =()=>{return reqaxios.get('/api/cart/cartList')}

  //添加到购物车 商品id 数量
export const postjiarugowuc = (skuId, skuNum) => { return reqaxios.post(`/api/cart/addToCart/${skuId}/${skuNum}`) }

  //切换商品选中状态
export const getsangpinxuanzong = (skuID, isChecked) => { return reqaxios.get(`/api/cart/checkCart/${skuID}/${isChecked}`) }

  //删除购物车商品
export const deletesangpinsancu = (skuId) => { return reqaxios.delete(`/api/cart/deleteCart/${skuId}`)}
