<script>
import Top from "@/components/Top/Top.vue";
import Dibu from "@/components/botto/dibu.vue";
import axios1 from "@/utlis/requext.js";
export default {
  data() {
    return {
      id: "",
      name: "",
      k: [],
      total: 0,
      pageNo: 1,
    };
  },
  components: { Top, Dibu },
  methods: {
    async luy(i, n, pageNo) {
      const luyiu = await axios1.post("/api/list", {
        category1Id: i,
        categoryName: n,
        pageNo,
        pageSize: 5,
      });
      // console.log(luyiu.data);
      this.k = luyiu.data.data;

      this.total = this.k.total;
      // console.log(i,n);
    },
    // 上一页
    prev() {
      this.pageNo -= 1;
      this.luy(this.id, this.name, this.pageNo);
    },
    // 下一页
    next() {
      this.pageNo += 1;
      this.luy(this.id, this.name, this.pageNo);
    },
    handleCurrentChange(o) {
      this.pageNo = o;
      this.luy(this.id, this.name, this.pageNo);
    },
    imgget(i,o){
      // this.$router.push({path:`/geren`,puery:{id:i}})
       this.$router.push({path:`/geren`,query:{id:i}})
      // console.log(this.id);
      console.log(i,o);
    }
  },
  created() {
    // console.log(this.$route.query);
    const k = this.$route.query;
    this.id = k.id;
    this.name = k.name;
    this.luy(this.id, this.name, this.pageNo);
  },
};
</script>

