<template>
  <div class="w">
    <div class="zuce">
      <el-form
        :model="ruleForm"
        status-icon
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="密码" prop="pass">
          <el-input
            type="password"
            v-model="ruleForm.pass"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item label="确认密码" prop="checkPass">
          <el-input
            type="password"
            v-model="ruleForm.checkPass"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item label="手机号" prop="age">
          <el-input v-model.number="ruleForm.age"></el-input>
        </el-form-item>
        <el-form-item label="验证码" prop="yz">
          <el-input v-model.number="ruleForm.yz"> </el-input
          ><button class="yanzm" @click="lol">获取验证码</button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')"
            >提交</el-button
          >
          <el-button @click="resetForm('ruleForm')">重置</el-button>
        </el-form-item>
      </el-form>
      <button @click="lol">77777</button>
    </div>
  </div>
</template>

<script>
import axiox1 from "@/utlis/requext";
export default {
  data() {
    var yanzm = (rule, value, callback) => {
      if (!value) {
        callback(new Error("请输入验证码"));
      } else {
        callback();
      }
    };
    // 手机号
    var checkAge = (rule, value, callback) => {
      let aa = "123";
      aa = "" + value;

      if (!aa) {
        return callback(new Error("手机号不能为空"));
      }

      setTimeout(() => {
        if (aa.length != 11) {
          return callback(new Error("手机号不正确"));
        } else {
          callback();
        }
      }, 1000);
    };
    // 密码
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        if (this.ruleForm.checkPass !== "") {
          this.$refs.ruleForm.validateField("checkPass");
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请再次输入密码"));
      } else if (value !== this.ruleForm.pass) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    return {
      ruleForm: {
        pass: "",
        checkPass: "",
        age: "",
        yz: "",
        uu: "嗷嗷嗷",
      },
      rules: {
        pass: [{ validator: validatePass, trigger: "blur" }],
        checkPass: [{ validator: validatePass2, trigger: "blur" }],
        age: [{ validator: checkAge, trigger: "blur" }],
        yz: [{ validator: yanzm, trigger: "blur" }],
      },
    };
  },
  methods: {
      submitForm(formName) {
       this.$refs[formName].validate((valid) => {
        if (valid) {
        this.yhuzuce(this.ruleForm.age,this.ruleForm.pass,this.ruleForm.yz)
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },

  //验证码
    async lol() {
      await this.$store.dispatch("zuceyanzma", this.ruleForm.age);
      this.ruleForm.yz = this.$store.state.Dlu.sjyanzma.data;
    },
    // 注册
    async yhuzuce(phone,password,code){
      try{
        await this.$store.dispatch("zanghaozuce", {
            phone,
            password,
            code,
          });
          this.$router.push('/login')
      }catch(error){
        
      }
      
    }
  },
};
</script>

<style lang="less" scoped>
* {
  margin: 0;
  padding: 0;
}
.w {
  width: 1200px;
  margin: auto;
}
.zuce {
  width: 300px;
  height: 500px;
}
/deep/ .el-form-item__content {
  display: flex;
  // border: 1px black solid ;
}
.yanzm {
  border: none;
  background: rgb(255, 255, 255);
  width: 150px;
  height: 40px;
  margin-left: 10px;
  z-index: 5;
  border-radius: 4px;
  border: 1px solid #dcdfe6;

  font-size: 3px;
}
</style>>
