<template>
  <div class="">
    <!-- <button><router-link to="/search">dianji</router-link></button> -->
    <!-- <router-view></router-view> -->
    <div class="fenl-box ">

      <router-link to="/search">dia</router-link>
      <div class="fenl-box-a " v-for="i in k" :key="i.categoryId" @mouseenter="enter(i.categoryId)" @mouseleave="entar" >
        
        <a  class="box-a" @click="luy(i.categoryName,i.categoryId)">{{i.categoryName}}</a>
        <div class="fenl-box-b" v-show="i.categoryId === id" >
          <dl  v-for="ii in i.categoryChild" :key="ii.categoryId">
            <dt @click="luy(ii.categoryName,ii.categoryId)">{{ii.categoryName}}</dt>
            <dd >
              <a  class=" box-b"  v-for="iii in ii.categoryChild" :key="iii.categoryId" @click="luy(iii.categoryName,iii.categoryId)">{{iii.categoryName}}</a>
              </dd>
          </dl>
        </div>

      </div>

    </div>


  </div>
</template>

<script>
import axios1 from "@/utlis/requext.js";
export default {
  data(){
    return{
      k:[],
      id:0
    }
  },
  methods:{
    async getaxios(){
      const res= await axios1.get("/api/product/getBaseCategoryList")
      this.k=res.data.data
      // console.log(this.k);
    },
    enter(i){
      // console.log(i);
      this.id=i
    },
    entar(){
      this.id=0
      // console.log(1);
    },
    luy(n,i){

      this.$router.push({path:`/search`,query:{id:i,name:n}})
      // console.log(this.$router);
      // this.$router.replace('/search')
    }

  },
  created(){
    this.getaxios()
  },

}
</script>

