// 登录退出状态显示模块
<template>
  <div>
    
    <div class="top-tiao-1"  v-if="code === 200 ">
      <samp>商品化欢迎您！</samp>
      <a href="" class="top-tiao-a-1">{{yh.data.data.name}}</a>
      <samp>|</samp>
      <a href="#" @click="tueicu">退出登录</a>
      
      
    </div>
    <div class="top-tiao-1" v-else >
      <samp>商品化欢迎您！</samp>
      
      <a @click="denglu">登录</a>
      <samp>|</samp>
      <a >免费注册</a>
    </div>
  </div>
</template>

<script>
export default {
  props:['token'],
  data() {
    return {
      yh:[],
      code:0
      
    };
  },

  methods: {
   async dengluget() {
      await this.$store.dispatch('yhuxingxi')
      this.yh=this.$store.state.Dlu.yhuxinxi
      this.code = this.yh.data.code
    },
   async tueicu(){
      await this.$store.dispatch('tueicudenglu')
      this.code =0
      this.$store.state.Dlu.token =''
    },
    denglu(){
      this.$router.push({path:'/login'})
    }
  },
  created() {
    this.dengluget()
  
  },
};
</script>

<style lang="less" scoped>
*{
  margin: 0px;
  padding: 0;

}
a,ul,li{

  text-decoration: none;
}
.top-tiao-a-1{
  color: #000;
  font-size: 10px;
  margin: 0 10px 0 10px;
}

</style>>