<template>
  <div class="wbox ">
     <!-- 放大镜 -->
    <div class="box">
      <div>
        <!-- 小图 -->
        <div
          class=" small-box"
          :style="{ width: width + 'px', height: height + 'px' }"
          @mouseover="over"
          @mousemove="move"
          @mouseout="out"
        >
          <img :src="uurl" alt="" />
          <div
            class="b small-box-zoom"
            v-show="a"
            :style="{
              width: w + 'px',
              height: h + 'px',
              transform: transformMask,
            }"
          ></div>
        </div>
        <!-- 大图 -->
        <div
          class="b small-box dabox"
          :style="{ width: w * beisu + 'px', height: h * beisu + 'px' }"
          v-show="a"
        >
          <img
            class="small-box-img"
            :src="uurl"
            alt=""
            :style="{
              width: 100 * beisu + '%',
              height: 100 * beisu + '%',
              transform: boximg,
            }"
          />
        </div>
      </div>
    </div>
     <!-- 轮播图 -->
    <div class="lbt ">
      <button @click="prev" :disabled="buttz"> < </button>
      <div class="llbt">
        <div class="lbt-box" :style="{ transform: lll }">
          <div class="lbt-box-a " v-for="i in uurlzu" :key="i.id" @click="dianjimgurl(i)">
            <img class="lbt-box-a-img" :src="i.imgUrl" alt="">
          </div>
        </div>
      </div>
      <button @click="next" :disabled="butty">></button>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    width: {
      default: 400,
      type: Number,
    },
    height: {
      default: 400,
      type: Number,
    },
    url: {
      default: ()=>{

      }

    },
    beisu: {
      default: 2,
      type: Number,
    },
    urlzu: {
      default: () => [],
    },
  },
  data() {
    return {
      a: false,
      imgobg: {},
      imgnom: {},
      imgx: 0,
      imgy: 0,
      transformMask: `translate(0px, 0px)`,
      boximg: `translate(0px, 0px)`,
      w: parseInt(this.width / this.beisu),
      h: parseInt(this.height / this.beisu),
      uurl: this.url || [{}],
      uurlzu:this.urlzu,

      lbjl: 0,
      lll: `translate(0px, 0px)`,
      buttz:true,
      butty:false,
    };
  },
  methods: {
    // 移入
    over() {
      this.a = true;
      this.imgobg = document.querySelector(".small-box");
      this.imgnom = this.imgobg.getBoundingClientRect();
      // console.log(this.width, this.height);
      // console.log(this.w/2);
      console.log(this.urlzu);
    },
    // 鼠标移动
    move(e) {
      this.imgx = parseInt(e.clientX - this.imgnom.x);
      this.imgy = parseInt(e.clientY - this.imgnom.y);
      document.querySelector(".small-box-zoom");

      // console.log(this.imgx,this.imgy);
      this.imgy = this.imgy < this.h / 2 ? this.h / 2 : this.imgy;
      this.imgx = this.imgx < this.w / 2 ? this.w / 2 : this.imgx;
      if (this.imgx > this.width - this.w / 2) {
        this.imgx = this.width - this.w / 2;
      }
      if (this.imgy > this.height - this.h / 2) {
        this.imgy = this.height - this.h / 2;
      }

      this.transformMask = `translate(${this.imgx - this.w / 2}px, ${
        this.imgy - this.h / 2
      }px)`;

      this.boximg = `translate(${-(this.imgx * this.beisu - this.w)}px, ${-(
        this.imgy * this.beisu -
        this.h
      )}px)`;
      // console.log(this.boximg);
      // console.log(this.w,this.h);
    },
    // 移出
    out() {
      this.a = false;
    },


    // 轮播图
    prev() {
      this.lbjl += 116;
      console.log(this.lbjl);
      this.lll = `translateX(${this.lbjl}px)`;
      console.log(this.uurlzu);
      this.but()
      
     
    },
    next() {
      console.log(456);
      this.lbjl -=116;
      this.lll =`translateX(${this.lbjl}px)`;
      this.but()
 
    },  

    but(){
      if(this.lbjl===0){
        this.buttz=true
      }else{
        this.buttz=false
      }

      const io = -(116* (this.uurlzu.length-3))
      if(this.lbjl=== io){
        this.butty =true
        console.log('***********');
      }else{
        this.butty=false
      }
    },

    dianjimgurl(i){
      this.uurl=i.imgUrl
      // console.log(-(116*this.uurlzu.length));

    }
  },
};
</script>

<style lang="less" scoped>
* {
  margin: 0;
  padding: 0;
}
a,
ul,
li {
  list-style: none;
}
.b{
  border: 1px black solid;
}
.wbox {
  height: 550px;
  width: 400px;
}
.box {
  // margin-left: 100px;
  height: 400px;
  width: 400px;
  position: relative;
}
.small-box-zoom {
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(101, 235, 49, 0.409);
}


.small-box {
  overflow: hidden;
  border: 1px rgb(186, 184, 184) solid;
}
.dabox{
  position: absolute;
  left: 400px;
  top: 0;
}
.small-box > img {
  width: 100%;
  height: 100%;
}

// 轮播图
.lbt {
  height: 100px;
  width: 400px;
margin-top: 2px;

  display: flex;
  justify-content: space-between;
}
.aaa {
  width: 100px;
  height: 98%;
}
.llbt {
  width: 360px;
  overflow: hidden;
}
.lbt-box{
  display: flex;


}
.lbt-box-a{
  width: 98px;
  flex-shrink: 0;
  height: 100px;
  margin-right: 16px;
}

button{
  width: 25px;

}
.lbt-box-a-img{
  width: 99%;
}
</style>>

