import { postzuce, getyanzma, postdenglu, getyhuxingxi, gettueicudenglu } from'@/api/getapi.js'

//登录小仓库

const state = {
  sjyanzma:'',
  token:'',
  yhuxinxi:'',
  
};
const getters = {

};
const mutations = {
  FETYANZM(state,sjyanzma){
    state.sjyanzma=sjyanzma
  },
  TOKEN(state,token){
    state.token=token
  },
  YHXINXI(state,yhuxinxi){
    state.yhuxinxi = yhuxinxi
  }


};
const actions = {
  //验证码
  async zuceyanzma({commit},uu){
    let yanzm = await getyanzma(uu)
    if (yanzm.data.code === 200) {
      commit('FETYANZM',yanzm.data)
      return 'ok'
    }else{
      return Promise.reject(new Error('777777'))
    } 
  },
  //注册
  async zanghaozuce({commit}, dda){
     await postzuce(dda)
  },
  //登录
  async yhudenglu({commit},dlx){
    let { data: tok }= await postdenglu(dlx)
    if (tok.code == 200) {
      commit('TOKEN', tok.data.token)
      return 'ok'
    }else{
      return Promise.reject(new Error('faile'))
    }
  },
  //用户信息
  async yhuxingxi({commit}){
    let yhxx=  await getyhuxingxi()
    commit('YHXINXI', yhxx);
  },
  //退出登录
  async tueicudenglu({commit}){
    let tc= await gettueicudenglu()
    console.log(tc);


  }

}
export default {
  state,
  getters,
  mutations,
  actions,
  
}