import Vue from 'vue'
import VueRouter from 'vue-router'

import Login from '@/views/Login/login.vue'
import Zuce from  '@/views/Login/zuce.vue'

import Home from '@/views/Home/home.vue'

import Remai from '@/views/paihb/remai.vue'
import Tejia from '@/views/paihb/tejia.vue'
import Xinpin from '@/views/paihb/xinpin.vue'

import Search from '@/views/search/search.vue'
import Geren from '@/views/search/geren.vue'
import Gowuc from '@/views/search/gowuc.vue'



Vue.use(VueRouter)

const routes = [
  { path: '/login', component: Login },
  {path:'/zuce',component:Zuce},
  {
    path: '/', component: Home,
    children: [
      { path: '', component: Remai },
      { path: 'tejia', component: Tejia },
      { path: 'xinpin', component: Xinpin }
    ],

  },
  { path: '/search',maem:'search', component: Search},
  { path: '/geren',name:'geren' , component: Geren},
  { path:'/gowuc',component:Gowuc},





]

const router = new VueRouter({
  routes
})

export default router
