<template>

<div>
  <Top></Top>
  <!-- 购物列表 -->
  <div class="gowulbiao-box w">
    <div>
      <h1>购物车</h1>
    </div>
    <hr>
    <dl>
      <dt>
        <samp> <input type="checkbox"  value="全选" @click="inputquanxuan"> 全选</samp><samp>商品信息</samp><samp>单价</samp><samp>数量</samp><samp>金额</samp><samp>操作</samp>
      </dt>
      <dd v-for="i in GowucLbiao" :key="i.id">
        <input type="checkbox" v-model="checked" @click="inputdanxuan(i.skuId)">
        <div >
          <img :src="i.imgUrl" alt="">
          <p>{{i.skuName}}</p>
        </div>
        <samp>￥{{i.cartPrice}}</samp>
        <samp><button @click="suliang(i.skuId,i.skuNum, -1)">-</button>{{i.skuNum}}<button @click="suliang(i.skuId,i.skuNum,1)">+</button></samp>
        <samp>{{i.cartPrice*i.skuNum}}</samp>
        <button @click="sancugowuc(i.skuId)">删除</button>

      </dd> 
    </dl>
  </div>
  <Bottom></Bottom>
</div>
</template>
<script>
import axios1 from'@/utlis/requext.js'
import router from '@/router'
import Top from'@/components/Top/Top.vue'
import Bottom from'@/components/botto/dibu.vue'
export default {
  data(){
    return{
      GowucLbiao:[],
      skuNumjia:1,
      skuNumjian:-1,
      checked:false
    }
  },
  components:{Top,Bottom},
  methods:{
    //购物车列表接口
   async getgouce(){
    //  this.GowucLbiao=  await axios1.get('/api/cart/cartList')
    //  console.log(this.GowucLbiao);
    await this.$store.dispatch('gowuclbiao') 
    this.GowucLbiao= await this.$store.state.Gowuc.gowuclbiao.data[0].cartInfoList 
    

    },
    //购物车商品数量修改
      //加 减
      async suliang(skuId,iskuNum,skuNum){
        console.log(skuId,iskuNum,skuNum);
        //加
        if(skuNum ===1){
          await this.$store.dispatch('jiarugowuc',{skuId, skuNum})
          this.getgouce()
        }else if(skuNum === -1){
          if (iskuNum >1) {
            await this.$store.dispatch('jiarugowuc',{skuId, skuNum})
          this.getgouce()
          }else{
            skuNum=0
            await this.$store.dispatch('jiarugowuc',{skuId, skuNum})
          this.getgouce()
          }
          

        }
      },
    //删除购物车商品
    async sancugowuc(skuId){
      await this.$store.dispatch('sangpinsancu',skuId)
      this.getgouce()
    },
    //全选
    inputquanxuan(e){
      console.log(e.target.checked);
      this.checked= e.target.checked
      this.inputdanxuan()

      
    },
    //单选
    inputdanxuan(skuid){
      console.log(skuid);
      console.log(1);

    }





  },
  created(){
    this.getgouce()
  }

}
</script>
